import { Typography } from '@mui/material';
import { useState } from 'react';
import Histogram from './Histogram';

function ScoreFeedback(props) {

    const createData = () => {
        let helperData = [];
        for (let i = 0; i <= props.numberOfQuestions; i++) {
            let frequency = 0;
            for (let j = 0; j < props.scores.length; j++) {
                if (props.scores[j] === i) {
                    frequency++;
                }
            }
            helperData = [...helperData, { Name: "Pisteitä", Score: i, Frequency: frequency }];
        }
        helperData[props.userScore].Frequency++;
        return ([...helperData]);
    };

    const createFeedback = () => {
        let helperFeedback = "";
        if (props.userScore === props.numberOfQuestions) {
            let numberOfFullScores = 0;
            for (let i = 0; i < props.scores.length; i++) {
                if (props.scores[i] === props.numberOfQuestions) {
                    numberOfFullScores++;
                }
            }
            helperFeedback = `Onneksi olkoon! Sait täydet pisteet! Täydet pisteet ${(props.questionLevel === "easy") ? "helpoista kysymyksistä" : "vaikeista kysymyksistä"} on saanut ${(props.scores.length > 0) ? ((numberOfFullScores / props.scores.length * 100).toFixed(0)) : "0"} prosenttia aiemmista vastaajista.`;
        } else if (props.userScore === 0) {
            helperFeedback = "Sait 0 pistettä. Yritä uudelleen.";
        } else {
            let betterPercent = 0;
            let betterScoreCount = 0;
            for (let i = 0; i < props.scores.length; i++) {
                if (props.scores[i] >= props.userScore) {
                    betterScoreCount++;
                }
            }
            betterPercent = betterScoreCount / props.scores.length;
            helperFeedback = `Saman tai paremman pistemäärän ${(props.questionLevel === "easy") ? "helpoista kysymyksistä" : "vaikeista kysymyksistä"} on saanut ${(props.scores.length > 0) ? ((betterPercent * 100).toFixed(0)) : "0"} prosenttia aiemmista vastaajista.`;
        }
        return (helperFeedback);
    }

    const [data, setData] = useState(createData());
    const [feedback, setFeedback] = useState(createFeedback());

    return (
        <div>
            <Typography
                sx={{ mb: 3, mt: 3 }}
                align='center'
                fontSize="1.6rem"
            >{feedback}
            </Typography>
            <Typography
                sx={{ mb: 3, mt: 3 }}
                align='center'
                fontSize="1.6rem"
            >{`Vastanneita yhteensä: ${props.scores.length + 1} kpl`}
            </Typography>
            <Histogram data={data} />
        </div>
    );
}

export default ScoreFeedback;