import { Button } from "@mui/material";

function OptionButton(props) {

    const checkAnswer = (option) => {
        if (props.buttonEnabled) {
            props.setShowContinue(true);
            props.checkAnswer(option.trim());
        }
    }

    return (
        <Button
            color={props.color}
            sx={{ p: 2, fontSize: "1.5rem", mb: 2 }}
            fullWidth
            variant="contained"
            onClick={(e) => { checkAnswer(e.target.innerText) }}
        >{props.children}
        </Button>
    )
}

export default OptionButton;