import { Button, ButtonGroup, Container, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import { useState } from "react";
import kuva from "./itameri.jpg";

function Home(props) {

    const [showNewGameButton, setShowNewGameButton] = useState(false);
    const [easy, setEasy] = useState("outlined");
    const [hard, setHard] = useState("outlined");

    const setEasyQuestions = () => {
        setEasy("contained");
        setHard("outlined");
        props.setQuestionLevel("easy");
        setShowNewGameButton(true);
    }

    const setHardQuestions = () => {
        setEasy("outlined");
        setHard("contained");
        props.setQuestionLevel("hard");
        setShowNewGameButton(true);
    }
    
    return (
        <Container maxWidth="sm">
            <img src={kuva} alt="itämeri" style={{width:"100%"}}/>
            <Typography variant="h5" sx={{mt:2}}>Testaa tunnetko oikeutesi ja velvollisuutesi liikkuessasi Itämerellä.</Typography>
            <Typography variant="h5" sx={{ mt: 5 }}>Valitse:</Typography>
            <ButtonGroup fullWidth >
                <Button
                    sx={{ p: 2, fontSize: "1.5rem" }}
                    variant={easy}
                    onClick={() => { setEasyQuestions() }}
                >Helpot kysymykset
                </Button>
                <Button
                    sx={{ p: 2, fontSize: "1.5rem" }}
                    variant={hard}
                    onClick={() => { setHardQuestions() }}
                >Vaikeat kysymykset
                </Button>
            </ButtonGroup>

            {(showNewGameButton)
                ? <Button
                    sx={{ p: 2, fontSize: "1.5rem", mt: 4 }}
                    variant="contained"
                    size="large"
                    fullWidth
                    component={Link}
                    to="/play"
                >Aloita peli
                </Button>
                : <p></p>}

        </Container>
    );
}

export default Home;