import { Button, Container, Grid, Grow, Paper, Stack, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import OptionButton from "./OptionButton";
//import Time from "./Time";

function Play(props) {

    const randomizeQuestions = () => {
        let questions = [...props.questions[props.questionLevel]];
        questions.sort((a, b) => 0.5 - Math.random());
        return ([...questions]);
        //setTimeIsGoing(true);
    };

    const [randQuestions, setRandQuestions] = useState(randomizeQuestions());
    const [questionNumber, setQuestionNumber] = useState(1);
    const [color1, setColor1] = useState("primary");
    const [color2, setColor2] = useState("primary");
    const [rightOrWrong, setRightOrWrong] = useState("");
    const [buttonEnabled, setButtonEnabled] = useState(true);
    const [numberOfCorrect, setNumberOfCorrect] = useState(0);
    const [timeIsGoing, setTimeIsGoing] = useState(false);
    const [resultTime, setResultTime] = useState();
    const [showContinue, setShowContinue] = useState(false);

    const [gameOver, setGameOver] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        if (props.questionLevel === "error") {
            navigate("/");
        }
    }, [props.questionLevel, navigate]);

    const nextQuestion = () => {
        setColor1("primary");
        setColor2("primary");
        setShowContinue(false);
        if (questionNumber < props.numberOfQuestions) {
            setQuestionNumber(questionNumber + 1);
            //setTimeIsGoing(true);
            setButtonEnabled(true);
        } else {
            //props.setUserTime(resultTime.toFixed(1));
            props.setUserScore(numberOfCorrect);
            setGameOver(true);
        }
    }

    useEffect(() => {
        if (gameOver === true) {
            navigate("/results");
        }
    }, [gameOver, navigate])

    const checkAnswer = (answer) => {
        if (answer === randQuestions[questionNumber - 1].answer) {
            setNumberOfCorrect(numberOfCorrect + 1);
            if (answer === "KYLLÄ") {
                setColor1("success");
                setRightOrWrong("Oikein meni!");
            } else {
                setColor2("success");
                setRightOrWrong("Oikein meni!");
            }
        } else if (answer === "KYLLÄ") {
            setColor1("error");
            setRightOrWrong("Väärin meni!");
        } else {
            setColor2("error");
            setRightOrWrong("Väärin meni!");
        }
        setButtonEnabled(false);
        //setTimeIsGoing(false);
    }

    return (
        <Container maxWidth="sm">
            <Stack
                direction="row"
                justifyContent={"space-between"}>
                <Typography
                    fontSize="2.4rem"
                    align="left"
                >{`Kysymys ${questionNumber}/${props.numberOfQuestions}`}
                </Typography>
                <Typography fontSize="2.4rem" align="right">
                    {`Pisteet: ${numberOfCorrect}`}
                </Typography>
            </Stack>
            <Paper sx={{ p: 2, mb: 3, mt: 3, textAlign: "center" }} elevation={2}>
                <Typography fontSize="1.4rem">{randQuestions[questionNumber - 1].question}</Typography>
            </Paper>
            <OptionButton color={color1} checkAnswer={checkAnswer} setShowContinue={setShowContinue} buttonEnabled={buttonEnabled}>
                {"KYLLÄ"}
            </OptionButton>
            <OptionButton color={color2} checkAnswer={checkAnswer} setShowContinue={setShowContinue} buttonEnabled={buttonEnabled}>
                {"EI"}
            </OptionButton>
            {(showContinue)
                ? <Container>
                    <Grow in={showContinue}>
                        <Paper sx={{ p: 2, mb: 3, textAlign: "center" }} elevation={2}>
                            <Typography fontSize="1.4rem">{rightOrWrong}</Typography>
                            <Typography fontSize="1.4rem">{randQuestions[questionNumber - 1].explanation}</Typography>
                        </Paper>
                    </Grow>
                    <Grid container justifyContent="flex-end">
                        <Button
                            color="primary"
                            variant="outlined"
                            onClick={() => { nextQuestion() }}
                            sx={{ p: 2, fontSize: "1.5rem" }}
                        >Jatka
                        </Button>
                    </Grid>
                </Container>
                : <p> </p>
            }
            <Button
                sx={{ mt: 10 }}
                fullWidth
                size="large"
                component={Link}
                to="/"
            >Keskeytä peli
            </Button>

        </Container >
    );
}

export default Play;