import { Typography } from '@mui/material';
import { useState } from 'react';
import { HashRouter, Route, Routes } from 'react-router-dom';
import Home from './components/Home';
import Play from './components/Play';
import Results from './components/Results';

function App() {

  const [questions, setQuestions] = useState({
    easy: [
      { question: "Olet veneretkellä saaressa. Saako rannalle tehdä nuotion ilman lupaa?", answer: "EI", explanation: "Tulenteko ei kuulu jokaisen oikeuksiin. Tulen voit tehdä virallisilla tulentekopaikoilla." },
      { question: "Olet veneilemässä eikä lähistöllä näy yhtäkään saarta tai venettä. Voit heittää mereen pienen muoviroskan, koska se kuitenkin hukkuu meren aaltoihin.", answer: "EI", explanation: "Luontoon ei saa heittää roskia. Muovi ei maadu vaan kerääntyy mereen." },
      { question: "Saako Itämerellä onkia ilman kalastuslupaa.", answer: "KYLLÄ", explanation: "Onkiminen kuuluu jokaisenoikeuksiin. Onkia voi ilman lupaa, paitsi erityisillä suojelualuilla." },
      { question: "Itämerellä saa veneillä ilmaiseksi.", answer: "KYLLÄ", explanation: "Vesialueilla liikkuminen kuuluu jokaisenoikeuksiin. Kunhan liikut häiritsemättä muita ihmisiä tai luontoa." },
      { question: "Onko uiminen sallittua muuallakin kuin virallisilla uimarannoilla?", answer: "KYLLÄ", explanation: "Uida saa kaikkialla, missä muutenkin saa liikkua." },
      { question: "Olet retkellä Ristisaaressa ja haluat tehdä tulentekopaikalle tulen, mutta polttopuuta ei näy missään. Onneksi saaressa saa kaataa puita ja katkoa oksia polttopuuksi.", answer: "EI", explanation: "Puiden kaato tai oksien katkominen on kiellettyä. Myös saarissa." },
      { question: 'Saarella on kyltti: "Luonnonsuojelualue, maihinnousu kielletty 1.4.-31.7." Saako saareen kesäkuussa rantautua.', answer: "EI", explanation: "Kielto koskee myös kesäkuuta. Kiellolla pyritään suojelemaan linnuston pesintää ja sitä on syytä noudattaa." },
      { question: "Olet perheen kanssa Lehmäsaaressa tulentekopaikalla paistamassa makkaraa, kun toinen porukka saapuu paikalle. Toisten pitää kunnioittaa rauhaanne ja jäädä odottamaan tulentekopaikan ulkopuolelle.", answer: "EI", explanation: "Teidän tulee ottaa huomioon muut luonnossaliikkujat. Tulipaikalla on tilaa kaikille!"},
      { question: "Kukourissa käydessäsi huomaat valkoposkihanhien ja lokkien pesiä kaikkialla. Haluaisit kierrellä linnoituksella, mutta pesät ovat edessä. Pitääkö sinun välittää pesivistä linnuista?", answer: "KYLLÄ", explanation: "Lintujen pesintää ei saa häiritä, vaikka pesät olisivat omasta mielestäsi väärässä paikassa."},
      { question: "Saako saarilla kerätä pihlajanmarjoja, vaikka ne olisivat puussa vielä kiinni?", answer: "KYLLÄ", explanation: "Marjoja saa poimia. Ei ole väliä ovatko marjat puussa vai varvuissa kiinni."}
    ],
    hard: [
      { question: "Näet veneestä maissa yksinäisen pienen laiturin, joka ei ole kenenkään pihapiirissä. Laiturissa ei ole kylttiä eikä ole tietoa kenen se on. Saat kiinnittyä laituriin ja nousta sille.", answer: "KYLLÄ", explanation: "Laituria saa käyttää ellei sen omistaja ole sitä erikseen kieltänyt esim. kyltillä." },
      { question: "Olet löytänyt eräältä saarelta kivan rantasataman kalliolta. Rantaudut sille usein ja mieleesi tulee pultin kiinnitys kallioon. Pysyvän porapultin saa kiinnittää tyhjälle kalliolle ilman maanomistajan lupaa rantautumisen helpottamiseksi.", answer: "EI", explanation: "Pysyvän porapultin asentamiseen tarvitaan maanomistajan suostumus." },
      { question: "Onko uistelukalastus kielletty Itäisen Suomenlahden kansallispuiston alueella?", answer: "EI", explanation: "Kansallispuiston alueella viehekalastamista koskevat normaalit säännöt ja rajoitukset." },
      { question: "Voiko maihinnousun saareen estää maanomistajan kieltokyltillä ilman erityistä perustetta?", answer: "EI", explanation: "Luonnonsuojelulaki (5 luku 36§) kieltää lakiin nojautumattoman maihinnousun kieltävän kyltin asettamisen." },
      { question: "Piha-alueiden rannasta on 50 metrin suojavyöhyke veteen päin, jossa veneet eivät saa liikkua eikä ankkuroitua.", answer: "EI", explanation: "Tämänkaltaista suojavyöhykettä ei ole olemassa. Vesialueella liikkumisesta ei kuitenkaan saa aiheutua tarpeetonta haittaa tai häiriötä. Kotirauhaa ei myöskään saa rikkoa." },
      { question: "Saako risukeitintä käyttää saaressa ruohikkopalovaroituksen aikana?", answer: "EI", explanation: "Risukeittimien käyttö ei ole sallittua ruohikko- ja metsäpalovaroituksen aikana siitä aiheutuvan kipinöintivaaran takia."},
      { question: "Käytte englantilaisen Sarahin kanssa veneilemässä Itämerellä ja kiertelemässä saaria. Saako Sarah kerätä saarilta puolukoita myyntiin, vaikka on kotoisin EU:n ulkopuolelta?", answer: "KYLLÄ", explanation: "Luonnontuotteiden kerääminen kuuluu jokaisenoikeuksiin tarkoitukseen tai kansallisuuteen katsomatta."},
      { question: "Saako hailia litkata ilman kalastuslupaa?", answer: "KYLLÄ", explanation: "Hailia eli silakkaa saa kalastaa ns. silakkalitkalla eli yhdellä vavalla siimaan kiinnitetyillä pystysuunnassa liikuteltavilla koukuilla."},
      { question: "Saako meren jäälle tehdä avotulen?", answer: "KYLLÄ", explanation: "Jäällä (toisin kuin maalla) saa tehdä nuotion."},
      { question: "Rantaudut juhannuksena asumattomalle saarelle. Saako koiran päästää vapaaksi saareen?", answer: "EI", explanation: "Koiraa ei saa pitää vapaana 1.3.-19.8. välisenä aikana, eikä muutenkaan ilman maanomistajan lupaa. Koirien kiinnipitoajan kunnioittaminen on tärkeää, jotta luonnoneläimillä olisi rauha niiden lisääntymisajan aikana. Vaikka koira ei olisikaan esimerkiksi linnuista kiinnostunut, linnut suhtautuvat koiraan kuin mihin tahansa petoon, ja kaikkoavat helposti pesimäpaikalta."},
      { question: "Laitesukeltaminen vaatii aina luvan, ellet ole sukellusseuran jäsen.", answer: "EI", explanation: "Sukeltaminen on vesialueiden yleiskäyttöä ja jokaisen oikeus. Sukeltamista koskevia rajoituksia on lähinnä rajavyöhykkeillä, suojelualueilla ja rauhoitettujen hylkyjen läheisyydessä."}
    ],
    error: ["error"]
  }
  );

  const [numberOfQuestions, setNumberOfQuestions] = useState(8);
  const [questionLevel, setQuestionLevel] = useState("error");
  const [userScore, setUserScore] = useState(0);
  const [userTime, setUserTime] = useState();

  const getInitialScore = () => {
    const storedScore = localStorage.getItem("scores");
    return storedScore ? JSON.parse(storedScore) : { easy: [], hard: [] };
  }

  const [scores, setScores] = useState(getInitialScore());

  const updateScores = (newScore, level) => {
    let newScores = { ...scores };
    if (level === "easy") {
      newScores.easy.push(newScore);
    }
    if (level === "hard") {
      newScores.hard.push(newScore);
    }
    setScores({...newScores});
    localStorage.setItem("scores", JSON.stringify(scores));
  }

  return (

    <HashRouter>

      <Typography
        fontSize="2.8rem"
        variant="h1"
        align='center'
        sx={{ mb: 5 }}
      >Jokaisenoikeudet Itämerellä
      </Typography>
      <Routes>
        <Route
          path="/"
          element={<Home
            setQuestionLevel={setQuestionLevel}
            questionLevel={questionLevel}
            questions={questions}
            scores={scores}
          />} />
        <Route
          path="/play"
          element={<Play
            questions={questions}
            numberOfQuestions={numberOfQuestions}
            setUserTime={setUserTime}
            setUserScore={setUserScore}
            questionLevel={questionLevel}
          />} />
        <Route
          path="/results"
          element={<Results
            numberOfQuestions={numberOfQuestions}
            scores={scores}
            updateScores={updateScores}
            userScore={userScore}
            userTime={userTime}
            setUserTime={setUserTime}
            setUserScore={setUserScore}
            questionLevel={questionLevel}
          />} />
      </Routes>

    </HashRouter>

  );
}

export default App;
