import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { Button, Container, Paper, Typography } from "@mui/material";
import ScoreFeedback from "./ScoreFeedback";

function Results(props) {
    const navigate = useNavigate();

    const setScoreAndGoHome = () => {
        props.updateScores(props.userScore, props.questionLevel);
        props.setUserTime(0);
        props.setUserScore(0);
        navigate("/");
    }

    useEffect(() => {
        if (props.questionLevel === "error") {
            navigate("/");
        }
    }, [props.questionLevel, navigate]);

    return (
        <>
            {(props.questionLevel === "error")
                ? <></>
                : <Container maxWidth="sm">
                    <Paper
                        sx={{ p: 2, textAlign: "center", mt: 3, /*backgroundColor: "#757de8",*/ color: "#757de8" }}
                        elevation={10}
                    >
                        <Typography fontSize="2.4rem">{`Pisteesi: ${props.userScore}`}</Typography>
                    </Paper>
                    <ScoreFeedback
                        numberOfQuestions={props.numberOfQuestions}
                        userScore={props.userScore}
                        scores={props.scores[props.questionLevel]}
                        questionLevel={props.questionLevel}
                    />
                    <Button sx={{ mt: 5 }} fullWidth variant="outlined" onClick={() => { setScoreAndGoHome() }}>Takaisin alkuun</Button>
                </Container >
            }
        </>
    );
}

export default Results;