import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, Label } from 'recharts';


function Histogram(props)  {

    return (
        <BarChart
            width={500}
            height={300}
            data={props.data}
            margin={{
                top: 5,
                right: 30,
                left: 20,
                bottom: 5,
            }}
        >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="Score">
                <Label value="Pisteitä" offset={0} position="insideBottom" />
            </XAxis>
            <YAxis dataKey="Frequency" allowDecimals={false} label={{ value: "Vastanneiden määrä (kpl)", angle: "-90", position: "insideBottomLeft" }} />
            <Tooltip />
            <Legend />
            <Bar dataKey="Frequency" fill="#8884d8" legendType='none' />
        </BarChart>
    );

}

export default Histogram;